  z-index: 3;
}
.tab__list {
  position: relative;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  margin: 0 -6.5px;
}
.tab__item {
  display: inline-block;
  position: relative;
  vertical-align: middle;
  padding: 0 6.5px;
}
.tab__item.active .tab__link {
  transform: translateX(0);
  transform-origin: left top;
}
