  color: #fff;
  background-color: transparent;
}
@media screen and (max-width: 960px) {
  .tab__item {
    width: 33.3333333333%;
  }
}
.tab__link {
  display: block;
  position: relative;
  width: 12.65rem;
  height: 2.9rem;
  color: #1389c7;
  font-weight: 500;
  font-family: "Roboto", sans-serif;
  font-size: 1.15rem;
  overflow: hidden;
  line-height: 2.8rem;
  background: #09bcab; /* Old browsers */ /* FF3.6-15 */ /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(135deg, #09bcab 6%, #1599cf 96%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr="#09bcab", endColorstr="#1599cf",GradientType=1 ); /* IE6-9 fallback on horizontal gradient */
  transition: 1s cubic-bezier(0.165, 0.84, 0.44, 1);
}
@media screen and (max-width: 960px) {
  .tab__link {
    width: 100%;
    font-size: 1rem;
  }
}
.tab__link span {
  display: block;
  position: absolute;
  top: 2px;
  left: 2px;
  background-color: #fff;
  width: calc(100% - 4px);
  height: calc(100% - 4px);
  line-height: 2.65rem;
  transition: transform 1s cubic-bezier(0.165, 0.84, 0.44, 1);
}
.tab__link::before {
  content: "";
  display: block;
  background-color: #1389c7;
  width: 120%;
  height: 101%;
  position: absolute;
  left: 0;
  top: 0;
  transform-origin: right top;
  transform: translateX(-101%);
  transition: transform 1s cubic-bezier(0.165, 0.84, 0.44, 1);
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
}
.tab__link.active span, .tab__link:hover span {
  color: #fff;
  background-color: transparent;
}
.tab__link.active::before, .tab__link:hover::before {
  transform: translateX(0);
  transform-origin: left top;
}

