  transition: 1s cubic-bezier(0.165, 0.84, 0.44, 1);
}

.js-btn,
.js-btn > * {
  transition: 1s cubic-bezier(0.165, 0.84, 0.44, 1);
}

.js-btn:before {
  display: block;
  content: "";
  position: absolute;
  top: 2px;
  left: 2px;
  width: calc(100% - 4px);
  height: calc(100% - 4px);
  background-color: #fff;
