.portfolio {
    position:relative;
    &__text {
        font-size:1.5rem;
        color: #222222;
        line-height:2rem;
        font-weight: 300;
        text-align:center;
    }
    &__title {
        font-size:2.25rem;
        line-height:1;
        color: #1389c7;
        font-weight: 500;
        font-family: 'Roboto','Noto Sans KR',sans-serif;
        text-align:center;
    }
    &__items {
        position:relative;
        display:flex;
        flex-wrap:wrap;
        flex-direction:row;
        padding-top:3rem;
        margin-left: -4px;
        margin-right: -2px;
        margin-top: -2px;
    }
    &__item {
        position:relative;
        width:calc(100% / 3);
        padding: 2px 2px 2px;        
        overflow:hidden;
        @media screen and (max-width:720px) {
            width:calc(100% / 2);
        }
        @media screen and (max-width:500px) {
            width:100%;
        }
        &__image{
            display:block;
            position:relative;
            width:100%;
            background-size:cover;
            background-position:50% 0;
            background-repeat:no-repeat;
            -webkit-transition: 1s cubic-bezier(.165, .84, .44, 1);
            transition: 1s cubic-bezier(.165, .84, .44, 1);
            &::before {
                content:"";            
                display:block;
                height:0;
                padding-top:90%;
            }
        }
        &:hover {
            .portfolio__item__image {
                transform:scale(1.1);
            }
        }
        &__type {
            display: block;
            font-size:0.8rem;
            color: rgba(255,255,225,0.7);
            line-height:0.8rem;
            font-family: 'Roboto',sans-serif;    
        }
        &__title {
            display: block;
            margin: 5px 0 20px;
            font-size:1.5rem;
            line-height:1.75rem;
            color: #fff;
            font-weight: bold;
        }
        &__btn-box {
            position:relative;
        }
        &__link {
            display: inline-block;
            padding: 0.4rem 1.3rem;
            font-size:0.75rem;
            color: #fff;
            border-radius: 50px;
            border: 1px solid #fff;
        }
    }
    &__inner {
        position:relative;
        width:100%;
        overflow:hidden;
    }
    &__link {
        position:relative;
        width:100%;
        &:hover {
            .portfolio__bg {opacity:1;}
            .portfolio__image {
                transform:scale(1.1);
                -webkit-transform:scale(1.1);
            }
        }
    }
    
    &__bg {
        position:absolute;
        top:0;
        left:0;   
        width:100%;
        height:100%;     
        z-index:2;
        opacity:0;
        background-color:rgba(0,0,0,0.76);
        -webkit-transition: 1s cubic-bezier(.165, .84, .44, 1);
        transition: 1s cubic-bezier(.165, .84, .44, 1);
        &__content {
            display:flex;
            flex-direction:column;
            justify-content:center;
            align-items:center;
            width:100%;
            height:100%;
            padding:10px;
            color:#fff;
        }
    }
    
}