.aboutus {
    position:relative;
    &__text {
        font-size:1.5rem;
        line-height:2rem;
        color: #222;
        font-weight:300;    
        text-align:center;
        &--large {
            font-size:2.25rem;
            line-height:2.25rem;
            color: #1389c7;
            font-weight: 500;        
        }
    }
    &__items {
        display:flex;
        flex-wrap:wrap;
        flex-direction:row;
        width:100%;
        padding-top:3rem;
        margin-bottom:3.5rem;
    }
    &__item {
        position:relative;
        width:calc(100% / 3);
        text-align:center;
        padding-bottom:2.5rem;
        @media screen and (max-width:874px) {
            width:100%;
            &:last-child {margin-top:3rem;}
        }
        &--left-line::after {
            display: block;
            content: "";
            position: absolute;
            top: 0px;
            right: 0;
            width: 1px;
            height: 100%;
            background-color: #ddd;
            @media screen and (max-width:874px) {
                top:auto;
                bottom:0;
                width:100%;
                height:1px;
            }
        }
        img {max-width:100%;}
        dl {margin-top:2rem;}
        dt {
            img {max-width:100%;}
        }
        dd {
            margin-top:1.25rem;
            font-size:0.8rem;
            color: #222222;
            line-height:1.35rem;
        }
    }
}

.company-info {
    width: 100%;
    padding:5rem 0;
    background: url(../img/info/about_comp_bg.jpg) no-repeat 50% 50%;
    background-size: cover;
    @media screen and (max-width:1023px) {
        padding:3.75rem 0;
    }
    .inner {
        display:flex;
        flex-wrap:wrap;
        flex-direction:row;
        align-items:center;
        @media screen and (max-width:720px) {
            flex-direction:column;
        }
    }
    &__logo {
        width: 437px;
        text-align: center;
        @media screen and (max-width:960px) {
            width:300px;
        }
        @media screen and (max-width:720px) {
            width:100%;
        }
        img {
            max-width:100%;
            @media screen and (max-width:720px) {
                max-width:200px;
            }
        }
    }
    &__content {
        position:relative;
        width: calc(100% - 470px);
        @media screen and (max-width:960px) {
            width: calc(100% - 300px);
        }
        @media screen and (max-width:720px) {
            width:100%;
            margin-top:1.5rem;
        }
    }
    ul {
        li {
            display:flex;
            flex-direction:row;
            flex-wrap:wrap;
            position:relative;
            width:100%;
            padding-left:1.1rem;
            font-size:1rem;
            color: #222;
            line-height:1rem;      
            &::before {
                display: block;
                content: "";
                position: absolute;
                top: 0.2rem;
                left: 0;
                width: 0.55rem;
                height: 0.55rem;
                background: url(../img/info/list_dot.png) no-repeat 0 0;
                background-size: 100% auto;
            }      
            + li {margin-top:1rem;}
            strong {
                display:block;
                width:5.5rem;
                font-weight:500;
            }
            span {
                display:block;
                width:calc(100% - 5.5rem);
            }
        }
    
    }
}

.history {
    position:relative;
    &__box + &__box {
        margin-top:5rem;
    }
    &__text {
        font-size:1.5rem;
        color: #222222;
        line-height:2rem;
        font-weight: 300;
        text-align:center;
    }
    &__title {
        font-size:2.25rem;
        line-height:1;
        color: #1389c7;
        font-weight: 500;
        font-family: 'Roboto','Noto Sans KR',sans-serif;
        text-align:center;
    }
    &__year {
        position:relative;
        &.fixed {
            position:fixed;
            z-index:-1;
            top:6.5rem;
            width:100%;
        }
        &__count {
            position: absolute;
            z-index: -1;
            top: 0;
            padding-top:1.75rem;
            width: 100%;
            font-size:7.5rem;
            font-family: 'Roboto',sans-serif;
            font-weight: bold;
            color: #dddddd;
            text-align: center;
        }
    }
    &__content {
        position:relative;
        margin-top:4.5rem;
        &::before {
            display: block;
            content: "";
            position: absolute;
            top: 6px;
            left: 50%;
            width: 1px;
            height:calc(100% - 4rem);
            background-color: #1389c7;
        }
        dl {
            position:relative;
            + dl {margin-top:5rem;}
            .circle {
                display: block;
                position: absolute;
                top: 7px;
                left: 3px;
                width:0.75rem;
                height:0.75rem;
                border-radius: 100%;
                background-color: #1389c7;
            }
            &.circle--1 {
                .circle {
                    top: 4px;
                    left:auto;
                    right:-3px;
                    width:1.1rem;
                    height:1.1rem;
                    background: #1592da;
                    background: -moz-linear-gradient(left, #1592da 9%, #01cc9f 90%);
                    background: -webkit-linear-gradient(left, #1592da 9%,#01cc9f 90%);
                    background: linear-gradient(to right, #1592da 9%,#01cc9f 90%);
                    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#1592da', endColorstr='#01cc9f',GradientType=1 );
                }
                dd {
                    padding-left:1.9rem;
                }
                dt {
                    padding-left:1.9rem;
                }
            }
            dt {
                position: relative;
                padding-left:1.5rem;
                font-size:1.5rem;
                color: #1389c7;
                line-height:1.5rem;
                font-weight: 700;
                font-family: 'Roboto',sans-serif;
            }
            
        }
        .dl--right {                
            padding-right:calc(50% - 8px);
            text-align: right;
            dd, dt {
                padding-left: 0;
                padding-right:1.5rem;
            }
            .circle {
                top: 4px;
                left:auto;
                right:0;
            }
        }
        .dl--left {
            padding-left:calc(50% - 8px);
        }
        dd {
            margin-top:0.75rem;
            padding-left:1.5rem;
            font-size:0.8rem;
            color: #555;
            line-height:1.25rem;
        }
    }
}

.org {
    &__text {
        font-size:1.5rem;
        color: #222222;
        line-height:2rem;
        font-weight: 300;
        text-align:center;
    }
    &__title {
        font-size:2.25rem;
        line-height:1;
        color: #1389c7;
        font-weight: 500;
        font-family: 'Roboto','Noto Sans KR',sans-serif;
        text-align:center;
    }
    &__content {
        margin-top:3rem;
        text-align:center;
        img {
            max-width:100%;
            &.pc {
                display:inline-block;
            }
            &.mobile {
                display:none;
            }
            @media screen and (max-width:960px) {
                &.pc {
                    display:none;
                }
                &.mobile {
                    display:inline-block;
                }
            }
        }
    }
}
.contactus {
    position:relative;
    &__title {
        font-size:2.25rem;
        line-height:1;
        color: #1389c7;
        font-weight: 500;
        font-family: 'Roboto','Noto Sans KR',sans-serif;
        text-align:center;
    }
    &__content {
        position:relative;
        margin-top:3rem;
    }
    &__map {
        position:relative;        
        z-index:2;
        &__content {
            position:relative;
            height:300px;
            background-color:#fff;
            border:1px solid #ddd;
            overflow:hidden;
        }
    }
    &__info {
        position: relative;
        z-index: 1;
        margin-top: -2.65rem;
        padding:5rem 0 2.45rem;
        background-color: #f9f9f9;
        .inner {
            display:flex;
            flex-direction:row;
            flex-wrap:wrap;
            align-items:flex-start;
            justify-content:space-between;
            width:100%;
            @media screen and (max-width:720px) {
                flex-direction:column;
            }
        }
        &__image {
            width:12.65rem;
            img {width:100%;}
            @media screen and (max-width:720px) {
                width:100%;
                img {width:200px;}
            }
        }
        &__content {
            position:relative;
            width:calc(100% - 16.35rem);
            @media screen and (max-width:720px) {
                width:100%;
                margin-top:1.5rem;
            }
            ul {
                position:relative;
                li {
                    position: relative;
                    padding-left:1.1rem;
                    font-size:1rem;
                    color: #222;
                    line-height:1;
                    margin-bottom:1rem;
                    &::before {
                        content: "";
                        display:block;
                        position: absolute;
                        top:5px;
                        left: 0;
                        width:0.55rem;
                        height:0.55rem;
                        background: url(../img/info/list_dot.png) no-repeat 0 0;
                        background-size: 100% auto;
                    }
                    .en {
                        font-family: 'Roboto',sans-serif;
                    }
                }
            }
        }
    }
    .section3 {
        padding-bottom:0;
    }
}