.technology {
    position:relative;
    &__text {
        font-size:1.5rem;
        color: #222222;
        line-height:2rem;
        font-weight: 300;
        text-align:center;
    }
    &__title {
        font-size:2.25rem;
        line-height:1;
        color: #1389c7;
        font-weight: 500;
        font-family: 'Roboto','Noto Sans KR',sans-serif;
        text-align:center;
    }
    &__info {
        display:flex;
        flex-wrap:wrap;
        flex-direction:row;
        align-items:center;
        width:100%;
        @media screen and (max-width:960px) {
            flex-direction:column;
            margin-top:2rem;
        }
        &__image {
            width:30rem;
            img {max-width:100%;}
            @media screen and (max-width:960px) {
                width:100%;
            }
        }
        &__content {
            position:relative;
            width:calc(100% - 30rem);
            @media screen and (max-width:960px) {
                width:100%;
                margin-top:1.5rem;
            }
        }
        &__text1 {
            font-size:1.25rem;
            color: #555555;
            line-height:1.5rem;
        }
        &__title {
            margin-top:0.5rem;
            font-size:2rem;
            line-height:2.5rem;
            color: #0091b5;
            font-weight: 500;
            letter-spacing: -0.1em;
        }
        &__text2 {
            margin-top:1rem;
            font-size:0.9rem;
            color: #555;
            line-height: 30px;
        }
        &__btn-box {
            margin-top:2.25rem;
            .js-btn + .js-btn {margin-left:0.5rem;}
        }
    }
    &__portfolio {
        margin-top: -0.875rem;
        padding-top: 2.2rem;
        margin-left: -0.875rem;
        margin-right: -0.875rem;
        text-align: center;
        &__item {
            display: inline-block;
            position:relative;
            vertical-align: top;
            padding:0.875rem;
        }
        &__btn-box {
            position: absolute;
            bottom:1.5rem;
            left:0;
            width:100%;
            text-align: center;
        }
        .pot_btn {
            display: inline-block;
            padding:0.5rem 1.5rem;
            vertical-align: top;
            background-color: rgba(0,0,0,0.7);
            text-align: center;
            font-size:0.75rem;
            line-height:1;
            &.cy {
                color:#c6ff2e;
            }
        }
    }
}