.board {
    position:relative;
    &__text {
        font-size:1.5rem;
        color: #222222;
        line-height:2rem;
        font-weight: 300;
        text-align:center;
    }
    &__title {
        font-size:2.25rem;
        line-height:1;
        color: #1389c7;
        font-weight: 500;
        font-family: 'Roboto','Noto Sans KR',sans-serif;
        text-align:center;
    }
}

.bbs-list {
    width:100%;    
    margin-top:3rem;
    border-top: 2px solid #1389c7;
    overflow:hidden;
    &__item {
        position: relative;
        border-bottom: 1px solid #ddd;
        padding:1.75rem 1rem;
        line-height:0.9rem;   
        cursor:pointer;     
        &__link {
            display:block;
            position:relative;
        }
        &__title {
            display: block;
            padding-left:1.1rem;
            padding-right:1.1rem;
            position: relative;
            font-size:1rem;
            color: #222;
            font-weight: 500;
            line-height:1.5rem;            
            &:before {
                display: block;
                content: "";
                position: absolute;
                top: 0.5rem;
                left: 0;
                background: url(../img/board/notice_dot.png) no-repeat 0 0;
                width:0.55rem;
                height:0.55rem;
                background-size: 100% auto;
            }
        }
        &__content {
            color: #555;
            margin-top:0.5rem;
            padding-left:1.1rem;
            height: 3.0em;
            overflow: hidden;
            font-size:0.8rem;
            line-height:1.25rem;
            -webkit-transition: 1s cubic-bezier(0.165, 0.84, 0.44, 1);
            transition: 1s cubic-bezier(0.165, 0.84, 0.44, 1);
            img {
                max-width: 100%;
            }
        }
        &__btn {
            display: block;
            position: absolute;
            top:2.25rem;
            right:1rem;
            width:0.55rem;
            height:0.5rem;
            background: url(../img/board/notice_arr.png) no-repeat 0 0;
            background-size: 100% auto;
            -webkit-transition: 1s cubic-bezier(0.165, 0.84, 0.44, 1);
            transition: 1s cubic-bezier(0.165, 0.84, 0.44, 1);
        }
        &__date {
            color: #999;
            margin-top: 0.75rem;
            padding-left:1.1rem;
            font-size:0.75rem;
            line-height:1;
        }
        &.active {
            .bbs-list__item__content {height:auto;}
            .bbs-list__item__btn {
                transform: rotate(180deg);
                -wekit-transform: rotate(180deg);
            }
            background-color:#f9f9f9;
        }
    }
    
}