.visual {
    position:relative;
    width:100%;
    &__slider {
        position:relative;
        width:100%;        
    }
    &__slide {
        position: relative;
        width: 100%;
        height: 100vh !important;
        background-repeat: no-repeat;
        background-size: cover;
        background-position: 50% 50%;
        padding-top:15.55rem;
        text-align:center;
        @media screen and (max-width:1023px) {
            padding-left:3%;
            padding-right:3%;
        }
        &--bg1 {
            background-image:url(../img/main/visual/vs_img1.jpg);
        }
        &--bg2 {
            background-image:url(../img/main/visual/vs_img2.jpg);
        }
        &--bg3 {
            background-image:url(../img/main/visual/vs_img3.jpg);
        }
        &--bg4 {
            background-image:url(../img/main/visual/vs_img4.jpg);
        }
        &--bg5 {
            background-image:url(../img/main/visual/vs_img5.jpg);
        }
        &--bg6 {
            background-image:url(../img/main/visual/vs_img6.jpg);
        }
    }
    &__title {       
        font-size:2.5rem;
        line-height:3.25rem;
        color: #fff;
        font-weight: 500;
        word-wrap: break-word;
        word-break: keep-all;
        font-family: "GmarketSansBold",sans-serif;
    }
    &__text {
        margin-top: 5px;
        font-size:1.4rem;
        color: #fff;
        line-height:1.5rem;
        word-wrap: break-word;
        word-break: keep-all;
    }
    &__btn-box {
        margin-top:1.4rem;
    }
    &__link {
        display: inline-block;
        position: relative;
        width:10.3rem;
        height:2.6rem;
        line-height:2.4rem;
        text-align: center;
        font-size:0.9rem;
        color: #fff;
        border: 2px solid #fff;
        border-radius: 50px;
        overflow: hidden;
        -webkit-transition: 1s cubic-bezier(.165, .84, .44, 1);
        transition: 1s cubic-bezier(.165, .84, .44, 1);
        span {
            position:relative;
            z-index:3;
        }
        &::before {
            content: '';
            display: block;
            background-color:rgba(16, 16, 16, 0.5);
            width: 120%;
            height: 101%;
            position: absolute;
            left: 0;
            top: 0;
            -webkit-transform-origin: right top;
            -ms-transform-origin: right top;
            transform-origin: right top;
            -webkit-transform: translateX(-101%) skewX(-17.62deg);
            -ms-transform: translateX(-101%) skewX(-17.62deg);
            transform: translateX(-101%) skewX(-17.62deg);
            -webkit-transition: -webkit-transform 1s cubic-bezier(.165, .84, .44, 1);
            transition: -webkit-transform 1s cubic-bezier(.165, .84, .44, 1);
            transition: transform 1s cubic-bezier(.165, .84, .44, 1);
            transition: transform 1s cubic-bezier(.165, .84, .44, 1), -webkit-transform 1s cubic-bezier(.165, .84, .44, 1);
            -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
        }
        &:hover {
            color:#fff;
            border-color: rgba(16, 16, 16, 0.5);
            &::before {
                -webkit-transform: translateX(0) skewX(-17.62deg);
                -ms-transform: translateX(0) skewX(-17.62deg);
                transform: translateX(0) skewX(-17.62deg);
                -webkit-transform-origin: left top;
                -ms-transform-origin: left top;
                transform-origin: left top;
            }
        }
    }
    .swiper-pagination-bullet {
        border-radius:50% !important;
        padding:0;
        margin:0;
        width:0.6rem;
        height:0.6rem;
        background-color:#fff;
        opacity:1;
        -webkit-transition: 1s cubic-bezier(.165, .84, .44, 1);
        transition: 1s cubic-bezier(.165, .84, .44, 1);
    }
    .swiper-pagination-bullet-active {
        width:1.65rem;
        border-radius:50px !important;
    }
}

.section1 {
    position:relative;
    width:100%;
    padding:4.25rem 0 4.75rem;
    z-index:2;
    background:url(../img/main/section1/sct1_bg.jpg);
    text-align:center;
    @media screen and (max-width:1023px) {
        padding-left:3%;
        padding-right:3%;
    }
    &__text {
        font-size:1.5rem;
        color:#fff;
        line-height:1.2;
    }
}

.section2 {
    position:relative;
    width:100%;
    background-color:#f1f1f1;
    padding:3.75rem 5.1rem;
    @media screen and (max-width:1023px) {
        padding-left:3%;
        padding-right:3%;
    }
    h2 {
        font-size:2.5rem;
        font-weight:500;
        color:#000;
        line-height:2.5rem;
        font-family:'Roboto',sans-serif;
        text-align:center;
    }
    &__btn-box {
        margin-top:1rem;
        text-align: center;
    }
    &__text {
        position:relative;
        margin-top: 1rem;
        font-size:0.9rem;
        color: #000;
        line-height:1.25rem;
        text-align: center;
    }
    &__link {
        display:inline-block;
        position:relative;
        width:8.35rem;
        height:2.15rem;
        line-height:1.75rem;
        text-align:center;
        font-size:0.9rem;
        color:#787878;
        border:2px solid #787878;
        border-radius:50px;
        overflow:hidden;
        &::before {
            content: '';
            display: block;
            background-color:rgba(16, 16, 16, 0.5);
            width: 126%;
            height: 110%;
            position: absolute;
            left: 0;
            top: 0;
            -webkit-transform-origin: right top;
            -ms-transform-origin: right top;
            transform-origin: right top;
            -webkit-transform: translateX(-101%) skewX(-17.62deg);
            -ms-transform: translateX(-101%) skewX(-17.62deg);
            transform: translateX(-101%) skewX(-17.62deg);
            -webkit-transition: -webkit-transform 1s cubic-bezier(.165, .84, .44, 1);
            transition: -webkit-transform 1s cubic-bezier(.165, .84, .44, 1);
            transition: transform 1s cubic-bezier(.165, .84, .44, 1);
            transition: transform 1s cubic-bezier(.165, .84, .44, 1), -webkit-transform 1s cubic-bezier(.165, .84, .44, 1);
            -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
        }
        &:hover {
            color:#fff;
        }
        &:hover::before {
            -webkit-transform: translateX(0) skewX(-17.62deg);
            -ms-transform: translateX(0) skewX(-17.62deg);
            transform: translateX(0) skewX(-17.62deg);
            -webkit-transform-origin: left top;
            -ms-transform-origin: left top;
            transform-origin: left top;
        }
        span {
            position:relative;
            z-index:2;
        }
    }
    .portfollo {
        margin-top:2.5rem;
        &__content {
            margin:-1.3rem;
            @media screen and (max-width:960px) {
                margin:-0.5rem;
            }
        }
        &__list {
            position:relative;
            width:100%;
            display:flex;
            flex-wrap:wrap;
            flex-direction:row;
            align-items:center;
            justify-content:center;
        }
        &__item {
            position:relative;
            width:33.333333333%;
            padding:1.3rem;
            @media screen and (max-width:960px) {
                padding:0.5rem;
            }
            @media screen and (max-width:640px) {
                width:50%;
            }
            @media screen and (max-width:600px) {
                width:100%;
            }
        }
        &__link {
            display:block;
            position:relative;
            width:100%;
        }
        &__image {
            position:relative;
            width:100%;
            background-repeat:no-repeat;
            background-size:cover;
            background-position:50% 50%;
            &::before {
                content:"";
                display:block;
                height:0;
                padding-top:75%;
            }
        }
        &__bg {
            position: absolute;
            display:flex;
            flex-direction:column;
            justify-content:center;
            align-items:center;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            z-index: 2;
            background-color: rgba(0,0,0,0.76);
            opacity: 0;
            -webkit-transition: 1s cubic-bezier(.165, .84, .44, 1);
            transition: 1s cubic-bezier(.165, .84, .44, 1);
            &__content {
                position:relative;
                display:flex;
                flex-wrap:wrap;
                flex-direction:column;
                justify-content:center;
                align-items:center;
                padding:0.5rem;                
                font-size:1.5rem;
                line-height:1.75rem;
                color: #fff;
                font-weight:700;
                text-align:center;
            }
        }
        &__link:hover {
            .portfollo__bg {opacity:1;}
        }
    }
}

