.inner {
    position:relative;
    width:100%;
    max-width:1300px;
    margin:0 auto;
    @media screen and (max-width:1300px){
        padding:0 3%;
    }
}

.header {
    position:absolute;
    top:0;
    left:0;
    width: 100%;
    height:5rem;
    z-index:99;
    @media screen and (max-width:960px) {
        height:auto;
        padding-top:1rem;
        padding-bottom:1rem;
    }
    &::before {
        content:"";
        position:absolute;
        top:0;
        left:0;
        width:100%;
        height:0;
        background-color:#fff;
        -webkit-transition: 0.2s cubic-bezier(.165, .84, .44, 1);
        transition: 0.2s cubic-bezier(.165, .84, .44, 1);
    }
    &__inner {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: space-between;
        align-items: center;
        width: 100%;
    }
    .logo {
        position:relative;
        &__link {
            display:inline-block;
            width:7.6rem;
            height:2.2rem;
            background-image:url(../img/common/logo.png);
            background-repeat:no-repeat;
            background-size:100% auto;
            background-position:0 0;
        }
    }
    .gnb {
        position:relative;
        @media screen and (max-width:960px) {
            display:none;
        }
        &__list {
            position:relative;
            display:flex;
            flex-wrap:wrap;
            flex-direction:row;
            align-items:center;
            margin:0 -1.8rem;
        }
        &__item {
            position:relative;
            &:first-child {
                padding-left:0;
                &::before {display:none;}
            }
            &:hover {
                .lnb {display:flex;}
            }
            &::before {
                display:block;
                content:"";
                position:absolute;
                top:2.45rem;
                left:0;
                width:0.2rem;
                height:0.2rem;
                background-color:#fff;
            }
        }
        &__link {
            display:block;
            position:relative;
            padding:0 1.8rem;
            font-size:1rem;
            color:#fff;
            font-weight:500;
            line-height:5rem;
            font-family: "Roboto","Noto Sans KR",sans-serif;
            &:hover {color:#fff;}
        }
    }
    .lnb {
        display:none;
        position:absolute;
        flex-wrap:wrap;
        flex-direction:row;
        width:20.5rem;
        top:3.7rem;
        left: -4rem;
        height: 1.8rem;
        line-height:1.65rem;
        padding: 0 1.75rem 0;
        background-color: #fff;
        border-radius: 50px;
        z-index: 3;
        &__item {
            padding: 0 7.5px;
            line-height:1.75rem;
        }
        &__link {
            font-size:0.75rem;
            color: #555;
            font-family: 'Roboto', 'Noto Sans KR', sans-serif;
            font-weight: 500;
        }
    }
    .btn-menu {
        display:none;
        width:50px;
        height:50px;
        background:url(../img/common/mobile_menu_btn_on.png) no-repeat 100% 50%;
        background-size:16px auto;
        z-index:4;
        cursor:pointer;
        @media screen and (max-width:960px) {
            display:block;
        }
    }
    &.fixed{
        position:fixed;       
        &::before {
            height:100%;
            background-color:#fff;
        }
        .gnb__link {color:#000;}
        .logo__link {
            background-image:url(../img/common/logo_on.png);
        }
        .btn-menu {
            background:url(../img/common/mobile_menu_btn.png) no-repeat 100% 50%;
            background-size:16px auto;
        }
    }
}

.mobile-gnb {
    display:none;
    position:fixed;
    top:0px;
    right:0px;
    width:100%;
    height:100%;
    margin:0;
    padding-top:0px;
    overflow:hidden;
    overflow-y:auto;
    z-index:999;
    transform:translateX(100%);
    -webkit-transform:translateX(100%);
    -moz-transform:translateX(100%);
    -ms-transform:translateX(100%);
    transition: transform 0.3s cubic-bezier(0.22, 0.61, 0.36, 1), -webkit-transform 0.3s cubic-bezier(0.22, 0.61, 0.36, 1);
    @media screen and (max-width:960px) {
        display:block;
    }
    &__head {
        top:0px;
        left:0px;
        width:100%;
        height:50px;
        z-index:3;
        background-color:#fff;
        &__logo {
            display:block;
            padding:14px 0 0 4%;
            text-align:left;
            &__link {
                display:block;
                width:76px;
                height:22px;
                background:url(../img/common/logo_on.png) no-repeat 0 0;
                background-size:100% auto;
            }
            img {width:110px;}
        }
        .btn-close {
            display:block;
            position:absolute;
            top:16px;
            right:4%;
            z-index:5;
            width:20px;
            height:20px;
            background:url(../img/common/mobile_menu_close.png) no-repeat 0 0;
            background-size:100% auto;
            cursor:pointer;
        }
    }
    &__body {
        padding:70px 3% 20px;
        height:100%;
        overflow-x: hidden;
        overflow-y: auto;
        z-index:2;
        background: rgba(20,135,201,0.95); /* Old browsers */
        background: -moz-linear-gradient(-45deg,rgba(20,135,201,0.95) 6%, rgba(1,188,146,0.95) 96%); /* FF3.6-15 */
        background: -webkit-linear-gradient(-45deg,rgba(20,135,201,0.95) 6%,rgba(1,188,146,0.95) 96%); /* Chrome10-25,Safari5.1-6 */
        background: linear-gradient(135deg, rgba(20,135,201,0.95) 6%,rgba(1,188,146,0.95) 96%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
        filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#1487c9', endColorstr='#01bb93',GradientType=1 ); /* IE6-9 fallback on horizontal gradient */
    }
    .gnb {
        width:100%;
        margin-top:4px;
        border-top:1px solid rgba(255,255,255,0.3);
        padding: 0 0 20px 0;
        &__list {
            position:relative;
            width:100%;
            overflow:hidden;
        }
        &__item {
            width:100%;            
            &.active {
                .gnb__link {
                    background-image:url(../img/common/mobile_dot.png)
                }
            }
        }
        &__link {
            display:block;
            padding-left:0px;
            height:40px;
            border-bottom:1px solid rgba(255,255,255,0.3);
            line-height:40px;
            font-size:14px;
            color:#fff;
            font-weight:500;
            background-repeat:no-repeat;
            background-size:8px auto;
            background-position:99% 17px;
            background-image:url(../img/common/mobile_dot_off.png);
            &--no-bg {
                background-image:none;
            }
        }
    }
    .submenu-2dept {
        display:none;
        &__item {
            width:100%;
        }        
        &__link {
            display:block;
            position:relative;
            padding-left:20px;
            height:34px;
            background-color:#fff;
            border-bottom:1px solid #e5e5e5;
            line-height:32px;
            font-size:14px;
            color:#333;
            &::before {
                display:block;
                content:"-";
                position:absolute;
                top:0px;
                left:10px;
                font-size:14px;
                color:#333;
            }
        }
        &--open {
            display:block;
        }
    }
    &--open {
        transform:translateX(0);
        -webkit-transform:translateX(0);
        -moz-transform:translateX(0);
        -ms-transform:translateX(0);
        .mobile-gnb__head {position:fixed;}
    }
}
.overlay {
    display:none;
    position:fixed;
    top:0px;
    left:0px;
    width:100%;
    height:100%;
    background-color:rgba(0,0,0,0.8);
    z-index:101;
}

