.container {
    position:relative;
    width:100%;
    max-width:100%;
    padding:0;
}

.section3 {
    padding:7.5rem 0 7.5rem;
    background:url(../img/main/section4/main_sct_bg3.jpg) no-repeat 50% 0%;
    background-size:cover;
    @media screen and (max-width:1023px) {
        padding:3.75rem 0;
    }
    h2 {
        font-size:2rem;
        color:#222222;
        font-weight:500;
        line-height:2rem;
        span {
            font-size:1rem;
            color:#999;
            line-height:1rem;
            font-weight:400;
            @media screen and (max-width:960px) {
                display:block;
                margin-top:0.5rem;
            }
        }
    }
    &__content {
        margin-top:1.35rem;
        padding:2.6rem 2.4rem;
        border:1px solid #ddd;
        @media screen and (max-width:1023px) {
            padding:1.5rem;
        }
    }
    .lable {
        display: inline-block;
        position: relative;
        padding-left:1.15rem;
        font-size:1rem;
        color: #333;
        font-weight: 500;
        &::before {
            display: block;
            content: "";
            position: absolute;
            top:0.55rem;
            left: 0;
            width:0.5rem;
            height:0.5rem;
            background: url(../img/main/section4/lbl_dot.png) no-repeat 0 0;
        }
    }
    .frm-input {
        width: 100%;
        height:2.2rem;
        background-color: #f1f1f1;
        border: 0;
        font-size:0.7rem;
    }
    textarea.frm-input {
        height:159px;
    }
    table {
        width: 100%;
        border-collapse: collapse;
        border: 0;
        th {
            text-align: left;
            padding: 5px;
        }
        td {
            text-align:left;
            padding:5px;
        }
        @media screen and (max-width:960px) {
            display:block;
            colgroup {display:none;}
            tbody,
            tr,
            td,
            th {display:block;}

        }
    }
    &__btn-box {
        position:relative;
        margin-top:1.5rem;
        text-align:center;
    }
    .btn-qa {
        display:inline-block;
        position:relative;
        overflow:hidden;
        width:11.15rem;
        height:2.65rem;
        line-height:2.25rem;
        text-align:center;
        color:#1389c7;
        font-size:1rem;
        font-weight:500;
        border-radius:50px !important;
        background: #1592da;
        background: -moz-linear-gradient(45deg, #1592da 6%, #01cc9f 96%);
        background: -webkit-linear-gradient(45deg, #1592da 6%,#01cc9f 96%);
        background: linear-gradient(45deg, #1592da 6%,#01cc9f 96%);
        filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#1592da', endColorstr='#01cc9f',GradientType=1 );
        -webkit-transition: 1s cubic-bezier(.165, .84, .44, 1);
        transition: 1s cubic-bezier(.165, .84, .44, 1);
        span {
            position:relative;
            z-index:2;
        }
        &::before {
            display:block;
            content:"";
            position:absolute;
            top:2px;
            left:2px;
            width:calc(100% - 4px);
            height:calc(100% - 4px);
            background-color:#fff;
            border-radius:50px;
            opacity:1;
        }
        &:hover {
            &::before {opacity:0;}
            span {color:#fff;}
        }
    }
    .cheackbox {
        position: relative;
        padding-left:0.9rem;
        font-size:0.8rem;
        vertical-align:middle;
        line-height:1;
        font-weight: 500;
        color: #555;
        cursor: pointer;
        + .cheackbox {margin-left:0.9rem;}
        @media screen and (max-width:1023px) {
            font-size:0.9rem;
        }
        &__mark {
            display: block;
            position: absolute;
            top:0.35rem;
            left: 0;
            width:0.75rem;
            height:0.7rem;
            cursor: pointer;
            &::after,
            &::before {
                display: block;
                content: "";
                position: absolute;
                top: 0;
                left: 0;
                width:0.75rem;
                height:0.7rem;
                background-repeat:no-repeat;
                background-size:100% auto;
                background-position:0 0;
            }
            &::before {
                background-image:url(../img/main/section4/check_icon.png);
            }
        }
        input[type="checkbox"]:checked ~ .cheackbox__mark::after {
            background-image:url(../img/main/section4/check_icon_on.png)
        }
        input[type="checkbox"]:checked ~ .cheackbox__mark::before {display:none}
    }
    button  {
        display:inline-block;
        vertical-align:top;
        margin:0;
        margin-left:0.5rem;
        padding:0;
        background-color:transparent;
        font-size:0.8rem;
        line-height:normal;
        color:#000;
    }
}

