.tab {
    position:sticky;
    top:5.5rem;
    margin-top:3rem;
    text-align:center;
    z-index:3;
    &__list {
        position:relative;
        display:flex;
        flex-direction:row;
        flex-wrap:wrap;
        justify-content:center;
        align-items:center;
        margin:0 -6.5px;        
    }
    &__item {
        display:inline-block;
        position: relative;
        vertical-align: middle;
        padding: 0 6.5px;
        &.active {
            .tab__link {
                -webkit-transform: translateX(0);
                -ms-transform: translateX(0);
                transform: translateX(0);
                -webkit-transform-origin: left top;
                -ms-transform-origin: left top;
                transform-origin: left top;
                span {
                    color:#fff;
                    background-color:transparent;
                }
            }
        }
        @media screen and (max-width:960px) {
            width:calc(100% / 3);
        }
    }
    &__link {
        display: block;
        position: relative;
        width:12.65rem;
        height:2.9rem;
        color: #1389c7;
        font-weight: 500;
        font-family: 'Roboto',sans-serif;
        font-size:1.15rem;
        overflow: hidden;
        line-height:2.8rem;
        background: #09bcab; /* Old browsers */
        background: -moz-linear-gradient(-45deg, #09bcab 6%, #1599cf 96%); /* FF3.6-15 */
        background: -webkit-linear-gradient(-45deg, #09bcab 6%,#1599cf 96%); /* Chrome10-25,Safari5.1-6 */
        background: linear-gradient(135deg, #09bcab 6%,#1599cf 96%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
        filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#09bcab', endColorstr='#1599cf',GradientType=1 ); /* IE6-9 fallback on horizontal gradient */
        -webkit-transition: 1s cubic-bezier(.165, .84, .44, 1);
        transition: 1s cubic-bezier(.165, .84, .44, 1);
        @media screen and (max-width:960px) {
            width:100%;
            font-size:1rem;
        }
        span {
            display:block;
            position:absolute;
            top:2px;
            left:2px;
            background-color:#fff;
            width:calc(100% - 4px);
            height:calc(100% - 4px);
            line-height:2.65rem;
            transition: transform 1s cubic-bezier(.165, .84, .44, 1), 
            -webkit-transform 1s cubic-bezier(.165, .84, .44, 1);
        }
        &::before {
            content: '';
            display: block;
            background-color:#1389c7;
            width: 120%;
            height: 101%;
            position: absolute;
            left: 0;
            top: 0;
            -webkit-transform-origin: right top;
            -ms-transform-origin: right top;
            transform-origin: right top;
            -webkit-transform: translateX(-101%);
            -ms-transform: translateX(-101%);
            transform: translateX(-101%);
            -webkit-transition: -webkit-transform 1s cubic-bezier(.165, .84, .44, 1);
            transition: -webkit-transform 1s cubic-bezier(.165, .84, .44, 1);
            transition: transform 1s cubic-bezier(.165, .84, .44, 1);
            transition: transform 1s cubic-bezier(.165, .84, .44, 1), -webkit-transform 1s cubic-bezier(.165, .84, .44, 1);
            -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
        }
        &.active,
        &:hover{
            span {
                color:#fff;
                background-color:transparent;
            }
        } 
        &.active::before,
        &:hover::before {
            -webkit-transform: translateX(0);
            -ms-transform: translateX(0);
            transform: translateX(0);
            -webkit-transform-origin: left top;
            -ms-transform-origin: left top;
            transform-origin: left top;
        }
    }

}

/* btn */
.js-btn {
    display:inline-block;
    position:relative;
    overflow:hidden;
    padding:0.7rem 2.25rem;
    font-size:0.9rem;
    color:#fff;
    border-radius:50px;
}
.js-btn {
    background: #1592da;
    background: -moz-linear-gradient(45deg, #1592da 6%, #01cc9f 96%);
    background: -webkit-linear-gradient(45deg, #1592da 6%,#01cc9f 96%);
    background: linear-gradient(45deg, #1592da 6%,#01cc9f 96%);
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#1592da', endColorstr='#01cc9f',GradientType=1 );
    -webkit-transition: 1s cubic-bezier(.165, .84, .44, 1);
    transition: 1s cubic-bezier(.165, .84, .44, 1);
}
.js-btn,
.js-btn > * {
    -webkit-transition: 1s cubic-bezier(.165, .84, .44, 1);
    transition: 1s cubic-bezier(.165, .84, .44, 1);
}
.js-btn:before {
    display:block;
    content:"";
    position:absolute;
    top:2px;
    left:2px;
    width:calc(100% - 4px);
    height:calc(100% - 4px);
    background-color:#fff;
    border-radius:50px;
    opacity:0;
}
.js-btn span {position:relative;z-index:2;}
.js-btn:hover span {color:#1389c7}
.js-btn:hover:before {opacity:1;}


.pagenav {
    margin-top:5rem;
    text-align: center;
    line-height:1.55rem;
    a {
        display: inline-block;
        width:1.55rem;
        height:1.55rem;
        vertical-align: top;
        margin: 0 5px;
        text-align: center;
        border-radius: 100%;
        font-family: 'Roboto',sans-serif;
        font-size:0.75rem;
        line-height:1.55rem;

    &.num {
        background-color: #ccc;
        color: #fff;
        -webkit-transition: 1s cubic-bezier(0.165, 0.84, 0.44, 1);
        transition: 1s cubic-bezier(0.165, 0.84, 0.44, 1);
        &.on, &:hover {
            background: #1592da;
            /* Old browsers */
            background: -moz-linear-gradient(-45deg, #1592da 9%, #01cc9f 89%);
            /* FF3.6-15 */
            background: -webkit-linear-gradient(-45deg, #1592da 9%, #01cc9f 89%);

            /* Chrome10-25,Safari5.1-6 */
            background: linear-gradient(135deg, #1592da 9%, #01cc9f 89%);
            /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
            filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#1592da', endColorstr='#01cc9f',GradientType=1 );
          /* IE6-9 fallback on horizontal gradient */
        }
    }
    &.page {
        background-repeat: no-repeat;
        background-size:0.6rem 0.9rem;
        background-position: 50% 50%;
            &.next {
                background-image: url(data:image/svg+xml;base64,);
            }
            &.prev {
                background-image: url(data:image/svg+xml;base64,);
            }
        }
    }
}
